body {
    // height: 100vh;
    margin: 0;
    overflow-x: hidden;
    background-image: url('../../assets/background.png');
    background-repeat: no-repeat;
    background-size: cover;
    // background-size: 100% 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
li {
    margin: 0px;
    padding: 0px;
}

ol,
ul,
nav {
    list-style: none;
    margin: 0;
    padding: 0px;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

textarea,
input,
button {
    outline: 0;
    resize: none;
}

a {
    text-decoration: none;
}

* {
    box-sizing: border-box;
}

.outline-primary {
    border: 1px solid red;
}